import React, { useState, useEffect } from 'react'
import { Container } from 'react-bootstrap'
import Markdown from 'markdown-to-jsx';

function Docs() {
    const [docs, setDocs] = useState('');
    const [markdown, setMarkdown] = useState('');
    let [ content, setContent] = useState({md: ""});

    useEffect(()=> {
       
        fetch('https://github.com/futurestages/OnBoardXR_Landing_Page/blob/gh-pages/welcome.md',
        {
        headers: {
          'Content-Type': 'application/vnd.github.v3+md',
          'Accept': 'application/vnd.github.v3+md'
        }
      })
            .then((res) => res.text())
            .then((md) => {
                setContent({ md })
            })
    }, [])

  return (
    <div>
        <Container>
            <div style={{marginTop:'20px', marginBottom:'20px'}}>

            </div>
        {content.md && <Markdown children={content.md}/>}
        </Container>
    </div>
  )
}

export default Docs